.team-container {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  text-align: center;
  background-color: #fff;
  color: #010606;
}


@import url('https://fonts.googleapis.com/css?family=Coustard:300,400&display=swap');
@import url(https://fonts.googleapis.com/css?family=Lato:300,900);



.meet-the-team {
  font-size: 5%;
  position: relative;
  top: 10%
}

.meet-the-team strong {
  /* font-family: Lato; */
  font-size: 40px;
}

.item {
  position: relative;
  top: 30%;
  width: 400px;
  height: 400px;
  background-color: white;
  position: relative;
  display: inline-block;
  margin: 1% 2% 1% 0%;
  overflow: hidden;
  box-shadow: 2px 3px 10px #000000;
}

.item img {
  width: 102%;
}

.item .overlay {
  color: #000;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0;
  transition: all 0.5s ease;
  position: absolute;
  top: 0;
  bottom: 0;
}

/* effect-clean*/
.clean .overlay span:nth-child(1) {
  position: absolute;
  left: 30%;
  top: 35%;
  font-size: 80px;
  font-weight: bold;
  font-family: coustard;
  transform: translateX(-50%);
}

.clean .overlay span:nth-child(2) {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 15px;
  font-weight: 550;
  transform: translateX(-50%);
}

.clean .overlay span:nth-child(3) {
  height: 3px;
  background-color: #000;
  position: absolute;
  top: 61.5%;
  left: 0;
}

.clean .overlay:hover {
  opacity: 0.93;
}

.clean .overlay:hover span:nth-child(1) {
  animation: slide 0.4s;
}

.clean .overlay:hover span:nth-child(2) {
  animation: slide 0.7s;
}

.clean .overlay:hover span:nth-child(3) {
  animation: line 0.5s forwards;
}

@keyframes slide {
  0% {
    transform: translateX(-10%);
  }

  100% {
    transform: translateX(-50%);
  }
}

@keyframes line {
  0% {
    width: 0;
  }

  100% {
    width: 50%
  }
}

/* effect-uncover*/

.uncover .overlay span:nth-child(1) {
  position: absolute;
  left: 50%;
  top: 35%;
  font-size: 80px;
  font-weight: bold;
  font-family: coustard;
  transform: translateX(-50%);
  opacity: 0;
}

.uncover .overlay span:nth-child(2) {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 15px;
  font-weight: 550;
  transform: translateY(-50%);
  transform: translateX(-50%);
  opacity: 0;
}

.uncover .overlay span:nth-child(3) {
  height: 30px;
  background-color: #000;
  position: absolute;
  top: 38%;
  opacity: 0;
}

.uncover .overlay span:nth-child(4) {
  height: 31%;
  background-color: #000;
  position: absolute;
  top: 50%;
  left: 25%;
  opacity: 0;
}

.uncover .overlay:hover {
  opacity: 0.9;
}

.uncover .overlay:hover span:nth-child(1) {
  animation: fadein 0.6s forwards;
  animation-delay: 0.5s;
}

.uncover .overlay:hover span:nth-child(2) {
  animation: fadein 0.6s forwards;
  animation-delay: 0.75s;
}

.uncover .overlay:hover span:nth-child(3) {
  animation: draw-left 0.7s;
}

.uncover .overlay:hover span:nth-child(4) {
  animation: draw-right 0.7s;
  animation-delay: 0.6s;
}

@keyframes draw-left {
  0% {
    opacity: 0;
    width: 0px;
    left: 75%
  }

  50% {
    opacity: 1;
    left: 25%;
    width: 200px;
  }

  100% {
    opacity: 0;
    width: 0px;
    left: 25%;
  }
}

@keyframes draw-right {
  0% {
    opacity: 0;
    width: 0px;
  }

  50% {
    opacity: 1;
    width: 200px;
  }

  100% {
    opacity: 0;
    width: 0px;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

/* effect-explode */
.explode .overlay span:nth-child(1) {
  position: absolute;
  left: 10%;
  top: 0%;
  font-size: 1200%;
  font-weight: bold;
  font-family: coustard;
  transform: translateX(-50%);
  opacity: 0;
}

.explode .overlay span:nth-child(2) {
  position: absolute;
  text-align: left;
  top: 40%;
  left: 10%;
  font-size: 15px;
  font-weight: 550;
  opacity: 0;
}

.explode .overlay span:nth-child(3) {
  position: absolute;
  top: 90%;
  left: 0;
  height: 60px;
  background-color: #000;
}

.explode .overlay:hover {
  opacity: 0.9;
}

.explode .overlay:hover span:nth-child(1) {
  animation: focus 0.4s forwards;
  animation-delay: 0.2s;
}

.explode .overlay:hover span:nth-child(2) {
  animation: focus 0.4s forwards;
  animation-delay: 0.4s;
}

.explode .overlay:hover span:nth-child(3) {
  animation: block 0.3s forwards;
  animation-delay: 0.15s;
}

@keyframes focus {
  0% {
    transform: scale(1.4) translateX(-30%);
    filter: blur(10px);
    opacity: 0;
  }

  100% {
    transform: scale(1) translateX(0%);
    filter: blur(0px);
    opacity: 1;
  }
}

@keyframes block {
  0% {
    width: 0;
    filter: blur(5px);
    opacity: 0;
  }

  100% {
    width: 100%;
    filter: blur(0px);
    opacity: 1;
  }
}

